@use "sass:map";
@import "../../style/pages/theme";
.content {
  color: white;
  background: map.get($theme-colors, "main");
  height: 20px;
  left: 20px;
  top: -2px;
  border-radius: 40px;
  display: inline-block;
  text-align: center;
  line-height: 20px;
  font-size: 13px;
  font-weight: 500;
  position: absolute;
  padding: 0px 7px;
  ul {
    list-style: none;
    display: inline-block;
    margin-right: 15px;
    align-items: center;
  
    li {
      position: relative;
    }
  }
}

